import React, { useState } from 'react';
import WeakDeclension from './WeakDeclension';
import MixedDeclension from './MixedDeclension';
import StrongDeclension from './StrongDeclension';
import AdjectiveDeclensionQuiz from './AdjectiveDeclensionQuiz';
import Carousel from './Carousel';
import './styles.css';

const Toggle = ({ label, isOpen, onToggle, children }) => {
  return (
    <div className="toggle-container">
      <div className="toggle-header" onClick={onToggle}>
        <span className={`toggle-icon ${isOpen ? 'open' : ''}`}></span>
        <span className="toggle-label">{label}</span>
      </div>
      {isOpen && <div className="toggle-content">{children}</div>}
    </div>
  );
};

const Home = () => {
  const [showWeakInfo, setShowWeakInfo] = useState(true);
  const [showMixedInfo, setShowMixedInfo] = useState(true);
  const [showStrongInfo, setShowStrongInfo] = useState(true);
  const [adjective, setAdjective] = useState('schn');

  const handleAdjectiveChange = (event) => {
    setAdjective(event.target.value);
  };

  return (
    <div>
      <div>
        <label>
           : 
          <input type="text" value={adjective} onChange={handleAdjectiveChange} />
        </label>
      </div>
      <section>
        <Toggle label="  (der/die/das/die)" isOpen={showWeakInfo} onToggle={() => setShowWeakInfo(!showWeakInfo)}>
          <p>
                     ,   dieser (), jener (), jeder (), welcher (), alle (), mancher ().
          </p>
          <WeakDeclension adjective={adjective} />
        </Toggle>
      </section>
      <section>
        <Toggle label="  (ein/ein/eine/-)" isOpen={showMixedInfo} onToggle={() => setShowMixedInfo(!showMixedInfo)}>
          <p>
                  ,   kein ()   ,   mein (), dein (), sein (), ihr (), unser (), euer ().
          </p>
          <MixedDeclension adjective={adjective} />
        </Toggle>
      </section>
      <section>
        <Toggle label="  ( )" isOpen={showStrongInfo} onToggle={() => setShowStrongInfo(!showStrongInfo)}>
          <p>
               ,      ,   "viel" (), "wenig" (), "mehrere" (), "einige" (), "ein bisschen" (), "ein paar" ().
          </p>
          <StrongDeclension adjective={adjective} />
        </Toggle>
      </section>
      <section>
        <h2>  </h2>
        <Carousel />
      </section>
    </div>
  );
};

export default Home;