import React, { useState } from 'react';
import HighlightTable from './HighlightTable';

const explanations = [
  {
    text: '      "e",  "en"   .',
    highlightEnding: ['e'],
    declensions: [' .'],
    selectedCase: 'all',
    selectedGender: 'all'
  },
  {
    text: '      ,   ,   es, e, er.',
    highlightEnding: ['e', 'es', 'er'], 
    declensions: [' .'],
    selectedCase: 'all',
    selectedGender: 'all'
  },
  {
    text: '  ,  Akkusativ  Maskulin  "en".',
    highlightEnding: 'en',
    declensions: [' .', ' .', ' .'],
    selectedCase: 'Akkusativ',
    selectedGender: 'm'
  },
  {
    text: '    ,    Plural  "en".',
    highlightEnding: 'en',
    declensions: [' .', ' .'],
    selectedCase: 'all',
    selectedGender: 'plural'
  }
];

const Carousel = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const { text, highlightEnding, declensions, selectedCase, selectedGender } = explanations[currentIndex];

  const nextExplanation = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % explanations.length);
  };

  const prevExplanation = () => {
    setCurrentIndex((prevIndex) => (prevIndex - 1 + explanations.length) % explanations.length);
  };

  return (
    <div>
      <p>{text}</p>
      <HighlightTable
        adjective="schn"
        highlightEnding={highlightEnding}
        declensions={declensions}
        selectedCase={selectedCase}
        selectedGender={selectedGender}
      />
      <div>
        <button onClick={prevExplanation}></button>
        <button onClick={nextExplanation}></button>
      </div>
    </div>
  );
};

export default Carousel;